header {
  .header-name {
    font-weight: normal;
    line-height: 1;
    letter-spacing: -2px;

    span {
      font-weight: bold;
    }
  }

  .dot {
    width: 5px;
    height: 5px;
    display: inline-block;
    background: rgb(200,42,36);
    border-radius: 50%;
    margin: 2px;
  }

  .header-contacts {
    color: #333;
    text-decoration: none;
    margin: 0 7px;

    &:after {
      content: '';
      display: inline-block;
      width: 1px;
      height: 15px;
      vertical-align: top;
      background: #333;
      margin-left: 10px;
    }

    &:last-of-type:after {
      display: none;
    }

    i {
      margin-right: 3px;
    }
  }

  .header-slogan {
    font-style: italic;
  }
}
