.content-block {
  &__header {
    position: relative;

    &:after {
      content: '';
      width: 100%;
      height: 1px;
      display: inline-block;
      background-color: #333;
      position: absolute;
      top: auto;
      left: 0;
      bottom: 7px;
      z-index: -1;
    }

    span {
      background-color: #fff;
      span {
        color: rgb(200,42,36);
      }
    }
  }

  &__country {
    color: rgb(200,42,36);
    font-style: italic;
  }

  &__year {
    font-style: italic;
    font-weight: lighter;
  }

  ul {
    margin: 5px 0;
    padding: 0 0 0 15px;
  }

  .link {
    text-decoration: none;
    color: #333;
  }
}